/* Navbar */

nav {
    background: #F9D120;
}

nav > header {
    height: 25vh;
}

header > img {
    height: 100%;
}

nav > div {
    height: 10vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.nav-btns{
    width: 100%;
}
.nav-btns li {
    display: inline-block;
    list-style: none;
    margin: 0% 2%;
    text-align: center;
    text-shadow: 1px 1px;
    font-weight: bold;
    font-size: 1.1em;
}

@media only screen and (max-width: 600px) {
    nav > header {
        height: 20vh;
    }

    nav > div {
        height: 18vh;
    }

    .nav-btns li {
        width: 30%;
        margin: 2% 0%;
        padding: 1%;
        border: solid 1px #000;
        -webkit-box-shadow: 5px 5px;
                box-shadow: 5px 5px;
        border-radius: 7%;
        background: #F9D120;
        font-weight: bold;
    }
}
/* Films */

.film-card {
    width: 20%;
    height: 20vh;
    background: #000;
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: 0 4% 2% 0;
    border-radius: 5%;
}

/* Film Detail */

.film-details{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-right: 6%;
}

.small-details{
    width:30%;
    display: inline-block;
}

.large-detail{
    width:60%;
    display: inline-block;
}

.related-details{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between; 
    width:80%;
    margin-top: 5%;
}

@media only screen and (max-width: 600px) {
    .film-card {
        width: 45%;
        height: 20vh;
        padding: 5% 4%;
    }
    
    .small-details,
    .large-detail{
        width:100%
    }

    .box-details{
        display: inline-block;
    }

    .related-details{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        width:100%;
    }
    .related-details p{
        border: solid 1px #000;
        -webkit-box-shadow: 5px 5px;
                box-shadow: 5px 5px;
        border-radius: 7%;
        padding: 3%;
        margin-bottom:3%;
        width: 70%;
        background: #F9D120;
        font-weight: bold;
    }
}

/* Breadcrumb */

.breadcrumb{
    width: 100%;
    padding: 3% 6%;
    font-size: 1.15em;
}

@media only screen and (max-width: 600px) {
    .breadcrumb{
        font-size: 1.1em;
        margin-top: 1%;
    }
}
/* Char*/

.char{
    background: #000000;
    height: 20vh;
    width:20%;
    color:white;
    margin: 0 4% 2% 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-radius: 5%;
}

.char-details{
    width:100%;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.box-details{
    margin-top: 2%;
    padding-right: 6%;
}

@media only screen and (max-width: 600px) {
    .char{
        height: 13vh;
        width:28%;
        padding: 5%;
        text-align: center;
    }

    .box-chars {
        margin-bottom: 5%;
    }
}

/* General */

*{
    margin: 0;
    padding:0;
    font-family: 'Open Sans', sans-serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.h-center {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.placeholder{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center; 
    -ms-flex-wrap: wrap; 
        flex-wrap: wrap;
    padding-left: 6%;
} 

@media only screen and (max-width: 600px) {
    .placeholder{
        margin-bottom: 4%;
        padding-left: 6%;
    }
}

.empty{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    height: 65vh;
}

.bold {
    font-weight: bold;
}

a{
    text-decoration: none;
    color:#222222;
}

a:hover,
.link:hover{
    color: #1786EB;
}





